import { atom } from "recoil";

export const VideoAtom = atom({
  key: "isVideo",
  default: {
    playing: false, // 
    muted: false, // 
    controls: false, //    
    pip: false, //pipmode
    volume: 0.5, // 
    playbackRate: 1.0, // 
    played: 0, //   (value)
    seeking: false, //   
    duration: 0, //  
    full: false, // 
    cc: false,
    playedSec: 0,
  },
  storage: sessionStorage,
});

export const RateAtom = atom({
  key: "isRate",
  default: false,
});

export const CCAtom = atom({
  key: "isCC",
  default: false,
});

export const GearAtom = atom({
  key: "isGear",
  default: false,
});

export const QuestionAtom = atom({
  //   
  key: "isQuestion",
  default: [],
  storage: sessionStorage,
});

//     
export const VideoTimeCheckAtom = atom({
  key: "isVideoTimeCheckAtom",
  default: 0,
  storage: sessionStorage,
});

export const FullAtom = atom({
  key: "isFullAtom",
  default: false,
  storage: sessionStorage,
});

export const SeekAtom = atom({
  key: "isSeekAtom",
  default: false,
  storage: sessionStorage,
});

export const OverlappingAtom = atom({
  key: "isOverlappingAtom",
  default: false,
  storage: sessionStorage,
});

export const UserTokenAtom = atom({
  key: "isUserAtom",
  default: "",
  storage: sessionStorage,
});

export const UrlAtom = atom({
  key: "isUrlAtom",
  default: "s",
  storage: sessionStorage,
});

export const CourseListAtom = atom({
  key: "isCourseListAtom",
  default: [],
  storage: sessionStorage,
});

export const QueryListAtom = atom({
  key: "isQueryListAtom",
  default: { userId: "", courseId: "", unitId: "" },
  storage: sessionStorage,
});

export const unitInfoAtom = atom({
  key: "isUnitListAtom",
  default: {
    time: "",
    title: "",
    unitId: "",
    rating: 0,
  },
});
